import helloTip from './hello-tip'
import {TEngine} from '../../types/engine'

/** 海星工具类 */
class HX {
  /** 平台对象 */
  private _engine: TEngine
  /** 初始化方法 */
  public init: Function
  /** 设置页面标题 */
  public setTitle: Function
  /** 获取页面参数 */
  public getParams: Function
  /** 跳转页面 */
  public jump: Function
  /** 获取应用和页面配置 */
  public getConfig: Function
  /** 根据id获取指定配置 */
  public getConfigById: Function
    /** 获取uid */
    public getUid: Function

  constructor (engine: TEngine) {
    /* 构建时打印提示信息 */
    helloTip()

    this._engine = engine

    this.init = this._engine.init
    this.setTitle = this._engine.setTitle
    this.getParams = this._engine.getParams
    this.jump = this._engine.jump
    this.getConfig = this._engine.getConfig
    this.getConfigById = this._engine.getConfigById
    this.getUid = this._engine.getUid
  }
}

export type IHx = HX

export default HX

