import setTitle from './setTitle'
import getParams from './getParams'
import getConfig from './getConfig'
import getUid from './getUid'
import {LOG_APP_VISIT} from './log'
import store from '../../core/store'
import {limitTimeWait} from '../../tool/race'

import {TInitOption} from '../../../types/initOption'

const startTime = Date.now()
let logged = false
declare let AlipayJSBridge: any

export default async function (option: TInitOption): Promise<any> {
  try {
    option = Object.assign({
      env: 'prod',
      onConfig: function (config: any) {
        setTitle(config && config.title)
      },
    }, option)

    store.env = option.env

    const params: any = getParams()
    store.urlParams = params

    console.log(option)

    /* 后台界面同步配置信息做可视化渲染 */
    if (params.editor) {
      window.addEventListener('message', function (e: any) {
        console.log('onmessage', e.data)
        if (e.data) {
          const type = e.data.type
          const payload = e.data.payload
          switch (type) {
            case 'back':
              history.back()
              break
            case 'config':
              setTitle(payload && payload.pageConfig && payload.pageConfig.title)
              option.onConfig(payload.pageConfig)
              store.config = payload.pageConfig
              break
          }
        }
      })
    }

    setTimeout(function () {
      if (logged) return false
      if (!store.config || !store.uid) return false

      // 定时3秒上报
      logged = true
      LOG_APP_VISIT()
    }, 3000)

    if ((window as any).AlipayJSBridge) {
      (window as any).AlipayJSBridge.call('getNetworkType', function(result) {
        console.log('获取网络状态：', result);
        store.networkInfo = result
      });
      (window as any).AlipayJSBridge.call('getSystemInfo', function(result) {
        console.log('获取设备信息：', result);
        store.systemInfo = result
      });
    }

    // 请求config，uid
    const [
      config,
      uid,
    ]: Array<any> = await Promise.all([
      getConfig(params.version),
      // limitTimeWait(
      //   getUid(params)
      //     .then(uid => uid)
      //     .catch(err => {
      //       console.log(err)
      //       return Promise.resolve(null)
      //     }), 500
      // )
    ])
    console.log({config, uid})
    setTitle(config && config.title)
    option.onConfig(config)
    store.config = config
    store.uid = uid
    const res: any = {
      params,
      config,
      uid,
    }

    // 拿到配置后上报
    const endTime = Date.now()
    if (endTime - startTime >= 3000 && !logged) {
      logged = true
      LOG_APP_VISIT()
    }


    return res
  } catch(err) {
    console.log(err)
    return Promise.reject(err)
  }
}
