import store from '../../core/store'
import {buryData} from '../../services/index'
import {getPath} from './getParams'
import {limitTimeWait} from '../../tool/race'
import {dvMdHost, mdHost } from '../../core/constants'


/**
 * 埋点
 * @param payload.spm
 * @param payload.action
 * @param payload.events
 * @param payload.other
 * @returns
 */
export const LOG_FUNC = async (payload) => {
  try {
  const {config, uid, urlParams, networkInfo, systemInfo, env = 'prod'} = store

    const {spm = {}} = payload
    // appId
    const a = 'aSF' + config.id
    const spm_value = [a]
    if (spm.p) {
      // pageId
      spm_value.push('p' + spm.p)
      if (spm.m) {
        // moduleId_（index + 1）
        spm_value.push('m' + spm.m)
        if (spm.b) {
          // b，或者多帧b_(index + 1)
          spm_value.push('b' + spm.b)
        }
      }
    }
    const path = getPath()
    const params: any = {
      app_id: 'SF' + config.id,
      app_ver: config.version,
      guideVersion: '',
      tenant_code: '', // 商户code
      uid,
      spm_value: spm_value.join('.'),
      action: payload.action,
      spm_time: Date.now(),
      resource_spm: '', // 访问来源spm
      mobile: '',
      browser: '',
      browser_core: '',
      channel: urlParams.channel || 'self',
      channel2: '',
      portraitCode: '',
      other: payload.other ? JSON.stringify(payload.other) : '',
      device_brand: systemInfo?.brand || '',
      device_model: systemInfo?.model || '',
      network: networkInfo ? networkInfo.networkInfo : '',
      os: systemInfo?.platform || '',
      os_version: systemInfo?.system || '',
      uri: path || '',
      user_agent: window.navigator?.userAgent,
      events: payload.events ? JSON.stringify(payload.events) : '',
    }
    if (process.env.NODE_ENV !== 'production' || process.env.REACT_APP_CONSOLE) {
      // 开发时加个参数方便查看
      params.spm_remark = payload.spm_remark || ''
    }
    console.log(params)
    console.log(env)
    const res = await limitTimeWait(
      buryData(
        params,
        {
          baseURL: config.buss_line === '双V' ? dvMdHost[env] : mdHost[env],
        }
      ),
      500
    );
    return res;
  } catch (err) {
    console.error(err);
    return Promise.resolve(null)
  }
}

export const LOG_APP_VISIT = () => {
  console.log('访问3秒上报')
  LOG_FUNC({
    action: 4,
    spm: {},
    events: {
      activity_during_visit: {"duration_time": 3}
    },
  })
}
