/**
 * 打印海星使用提示信息
 */
export default function (): void {
  console.warn('欢迎使用高阳-海星-sdk，请使用HX.init初始化项目')
  if (!((window as any).ant)) {
    console.warn('ios设置标题有问题，需在html手动引入脚本<script src="https://a.alipayobjects.com/g/component/antbridge/1.1.4/antbridge.min.js"></script>')
    console.log('如果前端路由跳转时标题不同，请使用HX.setTitle重新设置标题')
  }
}
