import qs from '../../tool/qs'
import getParams from './getParams'

export default function (url: string): void{
  if (/^(https?:\/\/|\/\/)/.test(url)) {
    // 绝对路径直接跳转
  } else {
    // 相对路径加参数
    const params: any = getParams()
    if (params.editor) {
      delete params.editor
    }
    const str: string = qs.stringify(params)
    if (/\?/.test(url)) {
      url += '&' + str
    } else {
      url += '?' + str
    }
  }
  console.log('即将跳转：', url)
  location.href = url
}
