import qs from '../../tool/qs'

const parseUrl = () => {
  const location = window.location
  // alert(location)
  const search = location.search.slice(1) || ''
  const hash = location.hash || ''
  let path = ''
  let hashSearch = ''
  if (hash.indexOf('?') > -1) {
    // 取hash中的？参数
    [path, hashSearch] = hash.split('?')
  } else {
    path = hash
  }
  path = path.split('#')[1]
  // 如果是vue或者react的hash路由，获取url参数要考虑hash中问号接的内容

  return {
    ...qs.parse(search),
    ...qs.parse(hashSearch),
    page: path
  }
}

export const getPath = () => {
  return parseUrl().page
}

export default function (): any {
  const params = parseUrl()
  return params || {}
}
