import axios from 'axios'

const instance = axios.create({
  timeout: 10000,
})

// Request 拦截器
instance.interceptors.request.use(
  config => {
    return config
  },
  error => {
    return Promise.reject(error)
  }
)
// Response 拦截器
instance.interceptors.response.use(
  response => {
    return response.data
  },
  error => {
    return Promise.reject(error)
  }
)

// 获取json
export const GET_JSON = (url) => {
  return instance.get(url)
}

//获取uid
export const GET_UID = (params) => {
  return instance.get(`/auth/getAlipayOpenId`, {
    baseURL: 'https://luna-web.iyoudui.com',
    params,
  })
}

/**
 * 埋点接口
 * @param {*} data.app_id  app_id  必填
 * @param {*} data.app_ver  app_verion 必填
 * @param data.tenant_code  商户code
 * @param {*} data.uid 用户id 必填
 * @param {*} data.spm_value  埋点值  必填
 * @param {*} data.action  动作类型，必填 1点击 2页面监测 3曝光
 * @param {*} data.spm_time 触发时间 必填
 * @param data.resource_spm 访问来源spm
 * @param {*} data.channel  渠道 必填
 * @param data.channel2  二级渠道
 * @param data.portraitCode  开通页ab测试
 * @param data.other  其他数据
 * @param {*} data.network 网络情况，通过UA识别 分 5g/4g/3g/wifi/unknow等 必填
 * @param {*} data.uri  当前页路径（绝对地址） 必填
 * @param {*} data.user_agent  User-Agent 必填
 * @param {*} data.events  事件json字符串 必填
 * @returns
 */
 export const buryData = (data, options) => {
  console.log(data, options)
  return instance.post('/spm/burydata', data, options)
}
