import { GET_JSON } from '../../services/index'
// import getParams from './getParams'

export default function getConfig (version: string): Promise<any> {
  let url: string
  if (version === 'current' || version === '' || version == null) {
    url = './config.json'
  } else {
    url = './config.' + version + '.json'
  }
  url += '?' + Date.now()
  return GET_JSON(url)
    .then(res => res)
    .catch(err => {
      console.error(err)
      return Promise.resolve(null)
    })
}

