export default {
  parse: (str = ''): any => {
    const arr = str.split('&')
    const obj = {}
    arr.forEach(item => {
      const index = item.indexOf('=')
      const key = item.slice(0, index)
      const val = item.slice(index + 1)
      obj[key] = val
    })
    return obj
  },
  stringify: (obj = {}): string => {
    const arr = []
    for (const key in obj) {
      arr.push(key + '=' + obj[key])
    }
    return arr.join('&')
  },
}
